import axios from "axios";
import React, { Component } from "react";
import {
  HiChevronDoubleLeft,
  HiChevronDoubleRight,
  HiChevronLeft,
  HiChevronRight,
} from "react-icons/hi";
import { ImFolderOpen } from "react-icons/im";
import { connect } from "react-redux";
import { Auth, PushPopupAlert, Socket } from "../../../actions";
import {
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
} from "../../../actions/document";
import {
  allVerifyDocument,
  getCandidateDocuments,
} from "../../../actions/document_validation";
import {
  UserAccessInterface,
  userPageLimit,
  VerifyDocumentInterface,
} from "../../../actions/users";
import { PopupAlertData } from "../../../components/Alert/Popup";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { Events } from "../../../interfaces";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import { DocumentReducerInterface } from "../../../reducers/document";
import { isEmptyOrSpaces } from "../../../shared/dataTest";
import {
  CandidateDetailsInterface,
  DocValidationStatusInterface,
  UserDocumentsInterface,
  UserInterface,
} from "../../../shared/interfaces";
import { API } from "../../../utils/api";
import { errorToText } from "../../../utils/errors";
import { getUserById } from "../../UserManagement/UserList";
import AllContentByDocument, {
  TableAllCandidatesInterface,
} from "./AllContentByDocument";
import AllFileView from "./AllFileView";

export type FC_DocValidationSocketDataType = {
  data:
    | VerifyDocumentInterface
    | UserDocumentsInterface
    | TableAllCandidatesInterface
    | CandidateDetailsInterface;
  currentPage: number;
  actionType:
    | "all-opened"
    | "all-closed"
    | "all-validated"
    | "application-opened"
    | "application-closed"
    | "application-validated";
  previous: string | null;
};

export type FC_DocValidationSocketType = {
  user_id: string;
  doc_id: string;
  validation_status: "waiting" | "approved" | "rejected";
};

export const getUsersByPage = (
  users: {
    data: UserInterface[];
    page: number;
  }[],
  page: number
): UserInterface[] | undefined => {
  const index: number = users.findIndex((item) => item.page === page);
  if (index >= 0) {
    let data: UserInterface[] = [];
    users[index].data.forEach((element) => {
      let _data = element;
      _data.user_id = element._id!;
      data.push(_data);
    });
    return data;
  }

  return undefined;
};

interface AppProps {
  auth: Auth;
  userDocuments: UserAccessInterface;
  documentReducer: DocumentReducerInterface;
  socket: Socket;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  getCandidateDocuments: Function;
  allVerifyDocument: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface AppStates {
  loadingUsers: boolean;
  loading: boolean;
  loadingRequest: boolean;
  loadingImage: boolean;
  currentPage: number;
  lastLoadedPage: number[];
  viewCandidate: boolean;
  viewDoc: boolean;
  filterByCategory: string;
  filterByCategoryId: string;
  filterBySubCategory: string;
  filterBySubCategoryId: string;
  responseMsg: string;
  response: "approved" | "rejected";
  isTeachingDocument: boolean;
  hasDisabilityDocument: boolean;
  error: { element: string; msg: string };
  document_id: string;
  document_index: number | null;
  candidate_id: string;
  actionTaken: "" | "approval" | "rejection";
  expiration_date: string;
  openedDocument: string[];
}

class AllCandidates extends Component<AppProps, AppStates> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loadingUsers: true,
      loading: true,
      loadingRequest: false,
      loadingImage: true,
      currentPage: 1,
      lastLoadedPage: [1],
      viewCandidate: false,
      viewDoc: false,
      filterByCategory: "",
      filterByCategoryId: "",
      filterBySubCategory: "",
      filterBySubCategoryId: "",
      responseMsg: "",
      response: "approved",
      document_id: "",
      candidate_id: "",
      error: {
        element: "",
        msg: "",
      },
      document_index: null,
      isTeachingDocument: false,
      hasDisabilityDocument: false,
      actionTaken: "",
      expiration_date: "",
      openedDocument: [],
    };
  }

  setLoadingUsers = (value: boolean) => {
    this.setState({ loadingUsers: value });
  };

  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };

  setLoadingImage = (value: boolean) => {
    this.setState({ loadingImage: value });
  };

  setCurrentPage = (value: number) => {
    this.setState({ currentPage: value });
  };

  setLastLoadedPage = (value: number[]) => {
    this.setState({ lastLoadedPage: value });
  };

  setDocument_index = (data: number | null) => {
    this.setState({ document_index: data });
  };

  setDocumentId = (data: string) => {
    this.setState({ document_id: data });
  };

  setCandidateId = (data: string) => {
    this.setState({ candidate_id: data });
  };

  setViewDoc = (state: boolean) => {
    this.setState({ viewDoc: state });
  };

  setResponseMsg = (data: string) => {
    this.setState({ responseMsg: data });
  };

  setResponse = (data: "approved" | "rejected") => {
    this.setState({ response: data, isTeachingDocument: false });
  };

  setError = (data: { element: string; msg: string }) => {
    this.setState({ error: data });
  };

  setActionTaken = (data: "" | "approval" | "rejection") => {
    this.setState({ actionTaken: data });
  };

  setIsTeachingDocument = (state: boolean) => {
    this.setState({ isTeachingDocument: state });
  };

  setHasDisabilityDocument = (state: boolean) => {
    this.setState({ hasDisabilityDocument: state });
  };

  setViewCandidate = (state: boolean) => {
    this.setState({ viewCandidate: state });
  };

  componentDidMount = () => {
    // LOADING DOCUMENT CATEGORIES ===============================
    if (
      this.props.documentReducer.documentCategory === null ||
      this.props.documentReducer.documentCategory.length <= 0
    ) {
      this.props.getDocumentCategory();
    }

    // LOADING DOCUMENT SUB CATEGORIES =========================
    if (
      this.props.documentReducer.documentSubCategory === null ||
      this.props.documentReducer.documentSubCategory.length <= 0
    ) {
      this.props.getDocumentSubCategory();
    }

    // LOADING DOCUMENT FIELDS =========================
    if (
      this.props.documentReducer.documentFields === null ||
      this.props.documentReducer.documentFields.length <= 0
    ) {
      this.props.getDocumentFields();
    }
    // END LOADING CANDIDATES WITH WAITING DOCUMENTS =================================
    if (!this.props.userDocuments.users) {
      this.props.getCandidateDocuments(
        1,
        (status: boolean, msg: string, users?: UserInterface[]) => {
          this.setLoadingUsers(status);
          this.setLoading(status);
          if (!status) {
            if (!isEmptyOrSpaces(msg)) {
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }

            if (users) {
              this.setLoadingUsers(false);
              this.setLoading(false);
              console.log("====================================");
              console.log({ setLoadingUsers: status });
              console.log("====================================");
            }
          }
        }
      );
    } else {
      this.setLoadingUsers(false);
      this.setLoading(false);
    }

    // this.props.socket.socket.on(Events.DOCUMENT_VALIDATION_CLIENT,())
    this.props.socket.socket.on(
      Events.DOCUMENT_VALIDATION_CLIENT,
      (data: FC_DocValidationSocketDataType) => {
        console.log(
          "=================DOCUMENT_VALIDATION_CLIENT==================="
        );
        console.log({ data });
        console.log(
          "=================DOCUMENT_VALIDATION_CLIENT==================="
        );

        // your function or codes here
        let opened_document = [...this.state.openedDocument];
        if (data.actionType === "all-opened") {
          if (data.previous !== null) {
            opened_document = opened_document.filter(
              (item) => item !== data.previous!
            );
          }
          opened_document.push(data.data.doc_id);
          console.log(
            "=================DOCUMENT_VALIDATION_CLIENT==================="
          );
          console.log({
            data,
            opened_document,
            orginal: this.state.openedDocument,
          });
          console.log(
            "=================DOCUMENT_VALIDATION_CLIENT==================="
          );
        } else if (data.actionType === "all-closed") {
          opened_document = opened_document.filter(
            (item) => item !== data.data.doc_id
          );
        } else if (data.actionType === "all-validated") {
          const api_redux: VerifyDocumentInterface =
            data.data as VerifyDocumentInterface;

          this.props.allVerifyDocument(api_redux, this.state.currentPage);
          opened_document.push(data.data.doc_id);
        }
        this.setState({ openedDocument: opened_document });
      }
    );
  };

  componentWillUnmount = () => {
    this.props.socket.socket.off(Events.DOCUMENT_VALIDATION_CLIENT);
  };

  FC_SocketServeAction = (data: FC_DocValidationSocketDataType) => {
    this.props.socket.socket.emit(Events.DOCUMENT_VALIDATION_SERVER, data);
  };

  reloadUsers = (page: number) => {
    if (!this.state.lastLoadedPage.includes(page)) {
      this.props.getCandidateDocuments(
        page,
        (status: boolean, msg: string, users?: UserInterface[]) => {
          this.setLoadingUsers(status);
          if (!status) {
            if (!isEmptyOrSpaces(msg)) {
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }

            if (users) {
              this.setLastLoadedPage([...this.state.lastLoadedPage, page]);
              this.setLoadingUsers(false);
              console.log("====================================");
              console.log({ setLoadingUsers: status });
              console.log("====================================");
            }
          }
        }
      );
    }
  };

  generatePages = (total_users: number): JSX.Element => {
    const totalPages = Math.ceil(total_users / userPageLimit);
    const startPage: number = 1;
    let _pages = Array.from(Array(totalPages + 1 - startPage).keys())
      .map((i) => startPage + i)
      .slice(0, 5);

    if (totalPages > 4 && this.state.currentPage > 2) {
      if (totalPages - this.state.currentPage > 1) {
        _pages = [
          this.state.currentPage - 2,
          this.state.currentPage - 1,
          this.state.currentPage,
          this.state.currentPage + 1,
          this.state.currentPage + 2,
        ];
      } else if (totalPages - this.state.currentPage === 1) {
        _pages = [
          this.state.currentPage - 3,
          this.state.currentPage - 2,
          this.state.currentPage - 1,
          this.state.currentPage,
          this.state.currentPage + 1,
        ];
      } else {
        _pages = [
          this.state.currentPage - 4,
          this.state.currentPage - 3,
          this.state.currentPage - 2,
          this.state.currentPage - 1,
          this.state.currentPage,
        ];
      }
    }

    let jumpToFirst: boolean = false,
      jumpToLast: boolean = false,
      moveBack: boolean = false,
      moveNext: boolean = false;

    if (totalPages > 1 && this.state.currentPage !== 1) {
      moveBack = true;
    }

    if (totalPages > 1 && this.state.currentPage !== totalPages) {
      moveNext = true;
    }

    if (totalPages > 3 && this.state.currentPage > 3) {
      jumpToFirst = true;
    }

    if (
      totalPages > 3 &&
      this.state.currentPage !== totalPages &&
      totalPages - this.state.currentPage > 2
    ) {
      jumpToLast = true;
    }

    return (
      <div className="flex text-gray-700">
        {jumpToFirst && (
          <button
            className="h-12 w-12 ml-1 flex justify-center items-center rounded-full bg-gray-100 hover:bg-gray-200 cursor-pointer"
            onClick={() => this.setCurrentPage(1)}
          >
            <HiChevronDoubleLeft className="w-6 h-6" />
          </button>
        )}
        {moveBack && (
          <button
            className="h-12 w-12 ml-1 flex justify-center items-center rounded-full bg-gray-100 hover:bg-gray-200"
            onClick={() => this.setCurrentPage(this.state.currentPage - 1)}
          >
            <HiChevronLeft className="w-6 h-6" />
          </button>
        )}
        <div className="flex h-12 font-medium rounded-full mx-1 bg-gray-100">
          {_pages.map((item, index) => (
            <button
              className={`w-12 md:flex justify-center items-center gap-2 rounded-full ${
                this.state.currentPage === item
                  ? "bg-gray-600 hover:bg-gray-500 text-white cursor-auto"
                  : "hover:bg-gray-200"
              }`}
              key={index}
              disabled={this.state.currentPage === item}
              onClick={() => {
                this.setCurrentPage(item);
                this.reloadUsers(item);
              }}
            >
              {item}
            </button>
          ))}
        </div>
        {moveNext && (
          <button
            className="h-12 w-12 mr-1 flex justify-center items-center rounded-full bg-gray-100 hover:bg-gray-200 cursor-pointer"
            onClick={() => {
              this.setCurrentPage(this.state.currentPage + 1);
              this.reloadUsers(this.state.currentPage + 1);
            }}
          >
            <HiChevronRight className="w-6 h-6" />
          </button>
        )}
        {jumpToLast && (
          <button
            className="h-12 w-12 mr-1 flex justify-center items-center rounded-full bg-gray-100 hover:bg-gray-200 cursor-pointer"
            onClick={() => {
              this.setCurrentPage(totalPages);
              this.reloadUsers(totalPages);
            }}
          >
            <HiChevronDoubleRight className="w-6 h-6" />
          </button>
        )}
      </div>
    );
  };

  getDocByDocId = (
    userList: UserInterface[],
    user_id: string,
    doc_id: string
  ): {
    user: UserInterface | undefined;
    doc: UserDocumentsInterface | undefined;
  } => {
    const user: UserInterface | undefined = userList.find(
      (user) => user.user_id === user_id
    );
    let doc: UserDocumentsInterface | undefined;
    if (user) {
      doc = user.details.find((doc) => doc.doc_id === doc_id);
    }

    return { user, doc };
  };

  FC_shortListing = async (candidate_id: string) => {
    const thisUser =
      this.props.userDocuments.users &&
      getUsersByPage(this.props.userDocuments.users, this.state.currentPage) &&
      getUserById(
        getUsersByPage(this.props.userDocuments.users, this.state.currentPage)!,
        candidate_id
      );
    if (thisUser) {
      try {
        const res = await axios.patch(
          `${API.APPLICATION}/apply/doccheckshortlist/${candidate_id}`,
          { email: thisUser.email, phone_number: thisUser.phone_number }
        );
        console.log({ FC_shortListing: res });
      } catch (error) {
        console.error({ error });
        this.props.PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
    } else console.error(`Candidate not found with this Id: ${candidate_id}`);
  };

  handleVerify = async () => {
    if (
      this.state.candidate_id.length > 0 &&
      this.state.document_id.length > 0 &&
      this.state.response
    ) {
      if (
        this.state.response === "rejected" &&
        !this.state.responseMsg.length
      ) {
        return this.setState({
          error: {
            element: "comment",
            msg: "Reason of rejection is required",
          },
          loadingRequest: false,
        });
      }

      this.setState({
        loadingRequest: true,
      });
      const thisCandidate = this.getDocByDocId(
        getUsersByPage(
          this.props.userDocuments.users!,
          this.state.currentPage
        )!,
        this.state.candidate_id,
        this.state.document_id
      ).user!;
      const api_body: VerifyDocumentInterface = {
        user_id: this.state.candidate_id,
        doc_id: this.state.document_id,
        validation_status:
          this.state.response === "approved"
            ? DocValidationStatusInterface.APPROVED
            : DocValidationStatusInterface.REJECTED,
        validated_by: {
          user_id: this.props.auth.user.user_id,
          comment: this.state.responseMsg,
          validation_status:
            this.state.response === "approved"
              ? DocValidationStatusInterface.APPROVED
              : DocValidationStatusInterface.REJECTED,
        },
        in_education: this.state.isTeachingDocument
          ? this.state.isTeachingDocument
          : thisCandidate.in_education,
        disability: this.state.hasDisabilityDocument
          ? this.state.hasDisabilityDocument
          : thisCandidate.disability,
        expiration_date: this.state.expiration_date,
      };
      const api_redux: VerifyDocumentInterface = {
        user_id: this.state.candidate_id,
        doc_id: this.state.document_id,
        validation_status:
          this.state.response === "approved"
            ? DocValidationStatusInterface.APPROVED
            : DocValidationStatusInterface.REJECTED,
        validated_by: {
          user_id: this.props.auth.user.user_id,
          comment: this.state.responseMsg,
          validation_status:
            this.state.response === "approved"
              ? DocValidationStatusInterface.APPROVED
              : DocValidationStatusInterface.REJECTED,
          date: "",
        },
        in_education: this.state.isTeachingDocument
          ? this.state.isTeachingDocument
          : thisCandidate.in_education,
        disability: this.state.hasDisabilityDocument
          ? this.state.hasDisabilityDocument
          : thisCandidate.disability,
        expiration_date: this.state.expiration_date,
      };

      try {
        const res = await axios.post<VerifyDocumentInterface>(
          `${API.USER}/docs/validateuserdoc`,
          api_body
        );
        if (res) {
          this.props.allVerifyDocument(api_redux, this.state.currentPage);
          this.FC_SocketServeAction({
            data: api_redux,
            currentPage: this.state.currentPage,
            actionType: "all-validated",
            previous: null,
          });
          this.FC_shortListing(this.state.candidate_id);
          this.props.PushPopupAlert({
            title: `Document ${this.state.response} successful`,
            theme: PopupAlertTheme.success,
          });

          this.setState({
            response: "approved",
            responseMsg: "",
            actionTaken: "",
            hasDisabilityDocument: false,
            isTeachingDocument: false,
          });
        }
      } catch (error) {
        console.error(error);
        this.props.PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
      this.setState({ loadingRequest: false });
    }
  };

  render() {
    if (
      this.state.loading === true ||
      this.props.userDocuments.users === null
    ) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading documents...
        </div>
      );
    }

    return (
      <div className="w-full my-5">
        {/* header title */}
        <div className="my-2 mx-2 rounded flex flex-row justify-between items-center">
          <div className="flex flex-row items-center gap-3">
            <ImFolderOpen className="text-6xl text-blue-600" />
            <div className="flex flex-col">
              <span className="text-blue-600 text-3xl font-bold">
                Document validation - All candidate(s)
              </span>
            </div>
          </div>
        </div>
        {/* end header title */}

        {/* ----------------------------------------------------------- */}

        <div className="bg-white shadow rounded border border-gray-2 p-2 w-full overflow-x-auto">
          {/* ################################################### */}

          {/* VIEW LISTS */}
          {!this.state.viewCandidate && (
            <section className="">
              <AllContentByDocument
                loadingCandidates={this.state.loadingUsers}
                loadingRequest={this.state.loadingRequest}
                filterByCategoryId={this.state.filterByCategoryId}
                filterBySubCategoryId={this.state.filterBySubCategoryId}
                candidateList={
                  getUsersByPage(
                    this.props.userDocuments.users,
                    this.state.currentPage
                  )!
                }
                documentCategories={this.props.documentReducer}
                setDocumentId={this.setDocumentId}
                setCandidateId={this.setCandidateId}
                setLoadingImage={this.setLoadingImage}
                setViewDoc={this.setViewDoc}
                setDocument_index={this.setDocument_index}
                currentPage={this.state.currentPage}
                openedDocument={this.state.openedDocument}
              />

              {!this.state.loadingUsers && (
                <div className="flex flex-col items-center my-3">
                  {this.props.userDocuments.total_users &&
                    this.generatePages(this.props.userDocuments.total_users)}
                </div>
              )}
            </section>
          )}
        </div>

        {/* ----------------------------------------------------------- */}
        {/* modal control */}
        {this.state.viewDoc &&
          this.state.document_index !== null &&
          getUsersByPage(
            this.props.userDocuments.users,
            this.state.currentPage
          ) &&
          this.getDocByDocId(
            getUsersByPage(
              this.props.userDocuments.users,
              this.state.currentPage
            )!,
            this.state.candidate_id,
            this.state.document_id
          ).doc &&
          this.getDocByDocId(
            getUsersByPage(
              this.props.userDocuments.users,
              this.state.currentPage
            )!,
            this.state.candidate_id,
            this.state.document_id
          ).user && (
            <Modal
              backDrop={true}
              theme={Themes.default}
              close={() => {}}
              displayClose={false}
              backDropClose={false}
              title=""
              widthSizeClass={ModalSize.maxWidth}
              padding={{ title: false, body: false, footer: false }}
            >
              <AllFileView
                error={this.state.error}
                doc={
                  this.getDocByDocId(
                    getUsersByPage(
                      this.props.userDocuments.users,
                      this.state.currentPage
                    )!,
                    this.state.candidate_id,
                    this.state.document_id
                  ).doc!
                }
                candidate={
                  this.getDocByDocId(
                    getUsersByPage(
                      this.props.userDocuments.users,
                      this.state.currentPage
                    )!,
                    this.state.candidate_id,
                    this.state.document_id
                  ).user!
                }
                responseMsg={this.state.responseMsg}
                loadingRequest={this.state.loadingRequest}
                setResponseMsg={this.setResponseMsg}
                setResponse={this.setResponse}
                handleVerify={this.handleVerify}
                setViewCandidate={this.setViewCandidate}
                setError={this.setError}
                setViewDoc={this.setViewDoc}
                setCandidateId={this.setCandidateId}
                setDocumentId={this.setDocumentId}
                actionTaken={this.state.actionTaken}
                setActionTaken={this.setActionTaken}
                document_index={this.state.document_index}
                setDocument_index={this.setDocument_index}
                candidateList={
                  getUsersByPage(
                    this.props.userDocuments.users,
                    this.state.currentPage
                  )!
                }
                isTeachingDocument={this.state.isTeachingDocument}
                setIsTeachingDocument={this.setIsTeachingDocument}
                hasDisabilityDocument={this.state.hasDisabilityDocument}
                setHasDisabilityDocument={this.setHasDisabilityDocument}
                loadingImage={this.state.loadingImage}
                setLoadingImage={this.setLoadingImage}
                expiration_date={this.state.expiration_date}
                setExpirationDate={(value: string) =>
                  this.setState({ expiration_date: value })
                }
                currentPage={this.state.currentPage}
                openedDocuments={this.state.openedDocument}
              />
            </Modal>
          )}
        {/* end modal control */}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  userDocuments,
  document,
  socket,
}: StoreState): {
  auth: Auth;
  userDocuments: UserAccessInterface;
  documentReducer: DocumentReducerInterface;
  socket: Socket;
} => {
  return {
    auth,
    userDocuments,
    documentReducer: document,
    socket,
  };
};

export default React.memo(
  connect(mapStateToProps, {
    allVerifyDocument,
    getCandidateDocuments,
    getDocumentCategory,
    getDocumentSubCategory,
    getDocumentFields,
    PushPopupAlert,
  })(AllCandidates)
);
