import React, { useState } from "react";
import { ImFilesEmpty } from "react-icons/im";
import { IoDocumentOutline } from "react-icons/io5";
import { connect } from "react-redux";
import { Socket } from "../../../actions";
import TableLoading from "../../../components/Loading/TableLoading";
import { Events } from "../../../interfaces";
import { GetDocTitle } from "../../../interfaces/document_validation";
import { StoreState } from "../../../reducers";
import { DocumentReducerInterface } from "../../../reducers/document";
import { isEmptyOrSpaces } from "../../../shared/dataTest";
import {
  UserDocumentsInterface,
  UserInterface,
} from "../../../shared/interfaces";
import { FC_DocValidationSocketDataType } from "./AllCandidates";

export interface TableAllCandidatesInterface {
  // DATA FROM MAIN PROPERTY
  user_id: string;
  n_id: string;
  fname: string;
  lname: string;
  phone_number: string;
  district: string;
  email: string;

  // NEW DATA GENERATED BAISED ON DETAILS PROPERTY
  main_doc_title: string;
  // DATA FROM DETAILS PROPERTY
  category_id: string;
  sub_category_id: string;
  category_subcategory_data: string;
  title: string;
  description: string;
  files: string;
  start_year: string;
  end_year: string;
  school: string;
  details_email: string;
  names: string;
  details_phone_number: string;
  company: string;
  year: string;
  doc_id: string;
  validation_status: "waiting" | "approved" | "rejected";
  validated_by: {
    user_id: string;
    comment: string;
    validation_status: "waiting" | "approved" | "rejected";
    date: string;
  } | null;
  approved?: string;
}

// Tobe replaced with Emmy's function
export const AllDocumentInArray = (
  docCategories: DocumentReducerInterface,
  DocumentsData: UserInterface[],
  dataType: "all" | "default" = "default"
): TableAllCandidatesInterface[] => {
  let documentsTemp: TableAllCandidatesInterface[] = [];
  for (let c in DocumentsData) {
    let details = DocumentsData[c].details;
    for (let d in details) {
      let candidate_detail: UserDocumentsInterface =
        DocumentsData[c].details[d];
      const validated_by: {
        user_id: string;
        comment: string;
        validation_status: "waiting" | "approved" | "rejected";
        date: string;
      } | null = candidate_detail.validated_by
        ? {
            user_id: candidate_detail.validated_by.user_id,
            comment: candidate_detail.validated_by.commet,
            validation_status: candidate_detail.validated_by.validation_status,
            date: candidate_detail.validated_by.date,
          }
        : null;

      if (
        candidate_detail.files !== null &&
        candidate_detail.files.length > 0 &&
        dataType === "default" &&
        candidate_detail.validation_status === "waiting" &&
        DocumentsData[c]._id
      ) {
        documentsTemp.push({
          // DATA FROM MAIN PROPERTY
          user_id: DocumentsData[c]._id!,
          n_id: DocumentsData[c].n_id!,
          fname: DocumentsData[c].user_info.fname,
          lname: DocumentsData[c].user_info.lname,
          phone_number: DocumentsData[c].phone_number,
          district: DocumentsData[c].user_info.district,
          email: DocumentsData[c].email,
          // NEW DATA GENERATED BAISED ON DETAILS PROPERTY
          main_doc_title: GetDocTitle(docCategories, candidate_detail).main,
          // DATA FROM DETAILS PROPERTY
          category_id: candidate_detail.category_id,
          sub_category_id: candidate_detail.sub_category_id,
          category_subcategory_data: candidate_detail.category_subcategory_data,
          title: candidate_detail.title,
          description: candidate_detail.description,
          files: candidate_detail.files,
          start_year: candidate_detail.start_year,
          end_year: candidate_detail.end_year,
          school: candidate_detail.school.title,
          details_email: candidate_detail.email,
          names: candidate_detail.names,
          details_phone_number: candidate_detail.phone_number,
          company: candidate_detail.company,
          year: candidate_detail.year,
          doc_id: candidate_detail.doc_id,
          validation_status: candidate_detail.validation_status,
          validated_by: validated_by,
          approved: candidate_detail.approved,
        });
      } else if (
        candidate_detail.files !== null &&
        candidate_detail.files.length > 0 &&
        dataType === "all" &&
        DocumentsData[c]._id
      ) {
        documentsTemp.push({
          // DATA FROM MAIN PROPERTY
          user_id: DocumentsData[c]._id!,
          names:
            DocumentsData[c].user_info.fname +
            " " +
            DocumentsData[c].user_info.lname,
          n_id: DocumentsData[c].n_id ? DocumentsData[c].n_id! : "",
          fname: DocumentsData[c].user_info.fname,
          lname: DocumentsData[c].user_info.lname,
          phone_number: DocumentsData[c].phone_number,
          district: DocumentsData[c].user_info.district,
          email: DocumentsData[c].email,
          main_doc_title: GetDocTitle(docCategories, candidate_detail).main,
          // DATA FROM DETAILS PROPERTY
          category_id: candidate_detail.category_id,
          sub_category_id: candidate_detail.sub_category_id,
          category_subcategory_data: candidate_detail.category_subcategory_data,
          title: candidate_detail.title,
          description: candidate_detail.description,
          files: candidate_detail.files,
          start_year: candidate_detail.start_year,
          end_year: candidate_detail.end_year,
          school: candidate_detail.school.title,
          details_email: candidate_detail.email,
          // names: candidate_detail.names,
          details_phone_number: candidate_detail.phone_number,
          company: candidate_detail.company,
          year: candidate_detail.year,
          doc_id: candidate_detail.doc_id,
          validation_status: candidate_detail.validation_status,
          validated_by: validated_by,
          approved: candidate_detail.approved,
        });
      }
    }
  }
  return documentsTemp;
};

const filterByType = (
  dataList: TableAllCandidatesInterface[],
  docType: string = "all",
  docSubType: string = "all"
): TableAllCandidatesInterface[] => {
  let result: TableAllCandidatesInterface[] = dataList;

  // FILTER BY DOC TYPE FIRST ==================================
  if (docType === "all" || isEmptyOrSpaces(docType)) {
    result = dataList;
  } else if (
    docType !== "all" &&
    docSubType !== "all" &&
    !isEmptyOrSpaces(docSubType)
  ) {
    // FILTER BY DOC SUB CATEGORY TYPE SECONDLY ==================================
    result = dataList.filter(
      (item) =>
        item.category_id === docType && item.sub_category_id === docSubType
    );
  } else if (docType !== "all") {
    // FILTER BY DOC TYPE FIRST ==================================
    result = dataList.filter((item) => item.category_id === docType);
  }
  return result;
};

type AllContentByDocumentProps = {
  loadingCandidates: boolean;
  loadingRequest: boolean;
  filterByCategoryId: string;
  filterBySubCategoryId: string;
  candidateList: UserInterface[];
  setDocumentId: (data: string) => void;
  setCandidateId: (data: string) => void;
  setViewDoc: (state: boolean) => void;
  documentCategories: DocumentReducerInterface;
  setDocument_index: (data: number | null) => void;
  setLoadingImage: (value: boolean) => void;
  currentPage: number;
  openedDocument: string[];

  // from redux
  socket: Socket;
};

const AllContentByDocument: React.FC<AllContentByDocumentProps> = (props) => {
  const [searchInput, setSearchInput] = useState<string>("");

  const searchData = (
    objList: TableAllCandidatesInterface[],
    text: string
  ): TableAllCandidatesInterface[] => {
    if (undefined === text || text === "") return objList;
    return objList.filter((product) => {
      let flag;
      // let dataKeys = product;

      for (let prop in product) {
        flag = false;
        if (
          prop === "n_id" ||
          prop === "fname" ||
          prop === "lname" ||
          prop === "phone_number" ||
          prop === "district" ||
          prop === "email" ||
          prop === "main_doc_title" ||
          prop === "title" ||
          prop === "names" ||
          prop === "validation_status"
        ) {
          if (product[prop]) {
            flag =
              product[prop]!.toString()
                .toLowerCase()
                .indexOf(text.toLowerCase()) > -1;
          }
        }
        if (flag) break;
      }
      return flag;
    });
  };

  const FC_SocketServeAction = (data: FC_DocValidationSocketDataType) => {
    props.socket.socket.emit(Events.DOCUMENT_VALIDATION_SERVER, data);
  };

  const DATA_Documents = AllDocumentInArray(
    props.documentCategories,
    props.candidateList
  ).filter((item) => props.openedDocument.includes(item.doc_id) === false);

  return (
    <div>
      <div className="w-full py-2">
        <input
          onChange={(e) => setSearchInput(e.target.value)}
          type="text"
          value={searchInput}
          placeholder="Search..."
          className="w-full bg-gray-100 py-2 px-3 rounded focus:border-gray-800"
          disabled={props.loadingCandidates || !props.candidateList.length}
        />
      </div>
      <table className="w-full shadow" id="report_content">
        <thead className="bg-blue-50 border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
          <tr>
            <td className="px-3 py-2">#</td>
            <td className="px-3 py-2">Names</td>
            <td className="px-3 py-2">National ID</td>
            <td className="px-3 py-2">Document</td>
            <td className="px-3 py-2">Status</td>
            <td className="px-3 py-2"></td>
          </tr>
        </thead>
        <tbody>
          {props.loadingCandidates ? (
            <TableLoading cols={6} />
          ) : DATA_Documents.length === 0 ||
            filterByType(
              DATA_Documents,
              props.filterByCategoryId,
              props.filterBySubCategoryId
            ).length === 0 ? (
            <tr>
              <td colSpan={6} className="px-2 py-2">
                <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                  <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                  <span className="text-sm font-bold">
                    No result found! Please try to search again
                  </span>
                </div>
              </td>
            </tr>
          ) : (
            searchData(
              filterByType(
                DATA_Documents,
                props.filterByCategoryId,
                props.filterBySubCategoryId
              ),
              searchInput
            ).map((doc, index) => (
              <tr
                key={Math.random()}
                className="border-b border-gray-200 cursor-pointer group hover:bg-gray-100"
                onClick={() => {
                  props.setViewDoc(true);
                  props.setDocumentId(doc.doc_id);
                  props.setCandidateId(doc.user_id);
                  props.setDocument_index(index);
                  props.setLoadingImage(true);
                  FC_SocketServeAction({
                    data: doc,
                    currentPage: props.currentPage,
                    actionType: "all-opened",
                    previous: null,
                  });
                }}
              >
                <td className="p-3 text-left whitespace-nowrap">{index + 1}</td>
                <td className="p-3 text-left">
                  <span>{doc.fname + " " + doc.lname}</span>
                </td>
                <td className="p-3 text-left">
                  <span>{doc.n_id}</span>
                </td>
                <td className="py-3 px-3 text-left font-medium text-lg">
                  {doc.main_doc_title}
                </td>
                <td className="py-3 px-3 text-left">
                  <span className="text-sm font-medium bg-yellow-800 py-1 px-2 rounded text-yellow-100 align-middle">
                    {doc.validation_status === "waiting"
                      ? "Pending"
                      : doc.validation_status}
                  </span>
                </td>
                <td className="text-right">
                  <button
                    className="bg-blue-500 group-hover:bg-blue-600 text-blue-50 font-bold py-1 px-1 inline-flex rounded focus:outline-none"
                    title="View documents"
                    disabled={props.loadingRequest}
                    onClick={() => {
                      props.setViewDoc(true);
                      props.setDocumentId(doc.doc_id);
                      props.setCandidateId(doc.user_id);
                      FC_SocketServeAction({
                        data: doc,
                        currentPage: props.currentPage,
                        actionType: "all-opened",
                        previous: null,
                      });
                      // setModalState(true);
                    }}
                  >
                    <IoDocumentOutline className="w-5 h-5" />
                    View doc
                  </button>
                </td>
              </tr>
            ))
          )}
        </tbody>
      </table>
    </div>
  );
};

const mapStateToProps = ({
  socket,
}: StoreState): {
  socket: Socket;
} => {
  return {
    socket,
  };
};

export default React.memo(connect(mapStateToProps)(AllContentByDocument));
