import React, { Component } from "react";
import { FaRegCheckCircle } from "react-icons/fa";
import { MdCancel } from "react-icons/md";

interface FooterInitialProps {
  docStatus: "waiting" | "approved" | "rejected";
  loading: boolean;
  loadingImage: boolean;
  setActionTaken: (state: "" | "approval" | "rejection") => void;
  setResponse: (state: "approved" | "rejected") => void;
  change_decision?: boolean;
  setChangeDecision?: (state: boolean) => void;
}

class FooterInitial extends Component<FooterInitialProps> {
  render() {
    return (
      <div className="flex flex-row md:flex-row justify-end w-full items-center gap-2 overflow-x-auto">
        {/* right part */}
        {(this.props.docStatus === "waiting" ||
          this.props.change_decision === true) && (
          <div className="flex items-center gap-2 px-2">
            <button
              className={`px-4 py-2 font-semibold text-red-50 rounded inline-flex items-center space-x-2 focus:outline-none ${
                this.props.loading || this.props.loadingImage
                  ? "bg-red-300 cursor-wait"
                  : "bg-red-800"
              }`}
              onClick={() => {
                this.props.setActionTaken("rejection");
                this.props.setResponse("rejected");
                this.props.setChangeDecision &&
                  this.props.setChangeDecision(false);
              }}
              // disabled={this.props.loading || this.props.loadingImage}
            >
              <MdCancel className="w-5 h-5" />
              <span>Reject</span>
            </button>
            <button
              className={`px-4 py-2 font-semibold text-blue-50 rounded inline-flex items-center space-x-2 focus:outline-none ${
                this.props.loading || this.props.loadingImage
                  ? "bg-blue-400 cursor-wait"
                  : "bg-blue-700"
              }`}
              onClick={() => {
                this.props.setActionTaken("approval");
                this.props.setResponse("approved");
                this.props.setChangeDecision &&
                  this.props.setChangeDecision(false);
              }}
              // disabled={this.props.loading || this.props.loadingImage}
            >
              <FaRegCheckCircle className="w-5 h-5" />
              <span>Approve</span>
            </button>
          </div>
        )}
        {/* end right part */}
      </div>
    );
  }
}

export default React.memo(FooterInitial);
