import React, { Component } from "react";
import { FaRegCheckCircle } from "react-icons/fa";
import { ImRadioChecked2, ImRadioUnchecked } from "react-icons/im";
import { RiArrowGoBackFill } from "react-icons/ri";

interface FooterResponseProps {
  disability: boolean;
  in_education: boolean;
  expiration: boolean;
  actionTaken: "" | "approval" | "rejection";
  responseMsg: string;
  loadingRequest: boolean;
  error: {
    element: string;
    msg: string;
  };
  isTeachingDocument: boolean;
  setIsTeachingDocument: (state: boolean) => void;
  hasDisabilityDocument: boolean;
  setHasDisabilityDocument: (state: boolean) => void;
  setActionTaken: (state: "" | "approval" | "rejection") => void;
  setResponseMsg: (state: string) => void;
  handleVerify: () => Promise<void>;
  setError: (data: { element: string; msg: string }) => void;
  expiration_date: string;
  setExpirationDate: (value: string) => void;
}

class FooterResponse extends Component<FooterResponseProps> {
  render() {
    return (
      <div className="flex flex-col md:flex-col items-end gap-1 w-full overflow-x-auto border-t-2 pt-3">
        {/* right part */}
        <div className="flex gap-2 w-full px-2">
          <span className="text-gray-500 italic">Decision:</span>
          <span
            className={`${
              this.props.actionTaken === "approval"
                ? "text-blue-700"
                : "text-red-800"
            } text-xl font-medium`}
          >
            {this.props.actionTaken === "approval" ? "Approved" : "Rejected"}
          </span>
        </div>
        {this.props.actionTaken === "approval" && !this.props.in_education && (
          <div className="w-full px-2 mb-1 flex gap-2 items-center">
            <span className="text-gray-500 italic">
              Is Document for teaching:
            </span>
            <div
              className={
                !this.props.loadingRequest ? "cursor-pointer" : "cursor-default"
              }
              onClick={() => {
                !this.props.loadingRequest &&
                  !this.props.isTeachingDocument &&
                  this.props.setHasDisabilityDocument(false);
                !this.props.loadingRequest &&
                  this.props.setIsTeachingDocument(
                    !this.props.isTeachingDocument
                  );
              }}
            >
              {this.props.isTeachingDocument ? (
                <ImRadioChecked2 className="text-blue-600 w-8" />
              ) : (
                <ImRadioUnchecked className="text-gray-600 w-8" />
              )}
            </div>
          </div>
        )}
        {this.props.actionTaken === "approval" && !this.props.disability && (
          <div className="w-full px-2 mb-1 flex gap-2 items-center">
            <span className="text-gray-500 italic">
              Is Document for disability:
            </span>
            <div
              className={
                !this.props.loadingRequest ? "cursor-pointer" : "cursor-default"
              }
              onClick={() => {
                !this.props.loadingRequest &&
                  !this.props.hasDisabilityDocument &&
                  this.props.setIsTeachingDocument(false);
                !this.props.loadingRequest &&
                  this.props.setHasDisabilityDocument(
                    !this.props.hasDisabilityDocument
                  );
              }}
            >
              {this.props.hasDisabilityDocument ? (
                <ImRadioChecked2 className="text-blue-600 w-8" />
              ) : (
                <ImRadioUnchecked className="text-gray-600 w-8" />
              )}
            </div>
          </div>
        )}
        {this.props.expiration && (
          <div className="w-full">
            <div className="w-full px-2 mb-1 flex gap-2 items-center">
              <span className="text-gray-500 italic">Expiration date:</span>
              <input
                type="date"
                placeholder={`expiration date`}
                className={`border border-${
                  this.props.error.element === "expiration" ? "red" : "gray"
                }-400 rounded px-3 py-1 focus:outline-none`}
                style={{ resize: "none" }}
                value={this.props.expiration_date}
                onChange={(e) => {
                  this.props.setExpirationDate(e.target.value);
                  this.props.setError({ element: "", msg: "" });
                }}
                disabled={this.props.loadingRequest}
              />
            </div>
            {this.props.error.element === "expiration" && (
              <p className="text-red-800">{this.props.error.msg}</p>
            )}
          </div>
        )}
        <div className="w-full px-2">
          <textarea
            placeholder={`Comment for the ${this.props.actionTaken}`}
            cols={50}
            rows={3}
            className={`border border-${
              this.props.error.element === "comment" ? "red" : "gray"
            }-400 rounded px-3 py-1 w-full focus:outline-none`}
            style={{ resize: "none" }}
            value={this.props.responseMsg}
            onChange={(e) => {
              this.props.setResponseMsg(e.target.value);
              this.props.setError({ element: "", msg: "" });
            }}
            disabled={this.props.loadingRequest}
          ></textarea>
          {this.props.error.element === "comment" && (
            <p className="text-red-800">{this.props.error.msg}</p>
          )}
        </div>
        <div className="flex flex-row gap-1 max-w-full px-2">
          <button
            className="bg-gray-800 px-4 py-2 font-semibold rounded text-white inline-flex items-center space-x-2 focus:outline-none"
            onClick={() => {
              this.props.setResponseMsg("");
              this.props.setActionTaken("");
              this.props.setError({ element: "", msg: "" });
            }}
            disabled={this.props.loadingRequest}
          >
            <RiArrowGoBackFill className="w-5 h-5" />
            <span>Back</span>
          </button>
          <button
            className="bg-blue-700 px-4 py-2 font-semibold rounded text-white focus:outline-none"
            disabled={this.props.loadingRequest}
            onClick={() =>
              window.confirm(
                `Are you sure you want to ${this.props.actionTaken} this document?`
              ) && this.props.handleVerify()
            }
          >
            {this.props.loadingRequest ? (
              <div className="">Loading...</div>
            ) : (
              <div className="inline-flex items-center space-x-2 gap-1">
                <FaRegCheckCircle className="w-5 h-5" />
                <span>Confirm</span>
              </div>
            )}
          </button>
        </div>
        {/* end right part */}
      </div>
    );
  }
}

export default React.memo(FooterResponse);
