import React from "react";
import { AiOutlineFullscreen } from "react-icons/ai";
import { BiZoomIn, BiZoomOut } from "react-icons/bi";
import { CgImage } from "react-icons/cg";
import { FaRegCheckCircle } from "react-icons/fa";
import {
  ImFolderOpen,
  ImRadioChecked2,
  ImRadioUnchecked,
} from "react-icons/im";
import { MdCancel, MdClose } from "react-icons/md";
import { RiArrowGoBackFill } from "react-icons/ri";
import ViewPDF from "../../../components/ViewPDF/ViewPDF";
import { API } from "../../../utils/api";
import {
  fullScreen,
  getFileType,
  isFileImage,
  zoomInOut,
} from "../../../utils/functions";
import NoSession from "../../SessionSettings/NoSession";
import { TableCandidatesInterface } from "../DocumentApplChecking/ContentByDocument";

interface FooterResponsePropInterface {
  disability: boolean;
  in_education: boolean;
  expiration: boolean;
  actionTaken: null | "approval" | "rejection";
  responseMsg: string;
  loadingRequest: boolean;
  error: {
    element: string;
    msg: string;
  };
  isTeachingDocument: boolean;
  setIsTeachingDocument: (state: boolean) => void;
  hasDisabilityDocument: boolean;
  setHasDisabilityDocument: (state: boolean) => void;
  setActionTaken: (state: null | "approval" | "rejection") => void;
  setResponseMsg: (state: string) => void;
  handleVerify: () => Promise<void>;
  setError: (data: { element: string; msg: string }) => void;
  expiration_date: string;
  setExpirationDate: (value: string) => void;
}

const FooterResponse: React.FC<FooterResponsePropInterface> = (
  props
): JSX.Element => {
  return (
    <div className="flex flex-col md:flex-col items-end gap-1 w-full overflow-x-auto border-t-2 pt-3">
      {/* right part */}
      <div className="flex gap-2 w-full px-2">
        <span className="text-gray-500 italic">Decision:</span>
        <span
          className={`${
            props.actionTaken === "approval" ? "text-blue-700" : "text-red-800"
          } text-xl font-medium`}
        >
          {props.actionTaken === "approval" ? "Approved" : "Rejected"}
        </span>
      </div>
      {props.actionTaken === "approval" && !props.in_education && (
        <div className="w-full px-2 mb-1 flex gap-2 items-center">
          <span className="text-gray-500 italic">
            Is Document for teaching:
          </span>
          <div
            className={
              !props.loadingRequest ? "cursor-pointer" : "cursor-default"
            }
            onClick={() => {
              !props.loadingRequest &&
                !props.isTeachingDocument &&
                props.setHasDisabilityDocument(false);
              !props.loadingRequest &&
                props.setIsTeachingDocument(!props.isTeachingDocument);
            }}
          >
            {props.isTeachingDocument ? (
              <ImRadioChecked2 className="text-blue-600 w-8" />
            ) : (
              <ImRadioUnchecked className="text-gray-600 w-8" />
            )}
          </div>
        </div>
      )}
      {props.actionTaken === "approval" && !props.disability && (
        <div className="w-full px-2 mb-1 flex gap-2 items-center">
          <span className="text-gray-500 italic">
            Is Document for disability:
          </span>
          <div
            className={
              !props.loadingRequest ? "cursor-pointer" : "cursor-default"
            }
            onClick={() => {
              !props.loadingRequest &&
                !props.hasDisabilityDocument &&
                props.setIsTeachingDocument(false);
              !props.loadingRequest &&
                props.setHasDisabilityDocument(!props.hasDisabilityDocument);
            }}
          >
            {props.hasDisabilityDocument ? (
              <ImRadioChecked2 className="text-blue-600 w-8" />
            ) : (
              <ImRadioUnchecked className="text-gray-600 w-8" />
            )}
          </div>
        </div>
      )}
      {props.expiration && (
        <div className="w-full">
          <div className="w-full px-2 mb-1 flex gap-2 items-center">
            <span className="text-gray-500 italic">Expiration date:</span>
            <input
              type="date"
              placeholder={`expiration date`}
              className={`border border-${
                props.error.element === "expiration" ? "red" : "gray"
              }-400 rounded px-3 py-1 focus:outline-none`}
              style={{ resize: "none" }}
              value={props.expiration_date}
              onChange={(e) => {
                props.setExpirationDate(e.target.value);
                props.setError({ element: "", msg: "" });
              }}
              disabled={props.loadingRequest}
            />
          </div>
          {props.error.element === "expiration" && (
            <p className="text-red-800">{props.error.msg}</p>
          )}
        </div>
      )}
      <div className="w-full px-2">
        <textarea
          placeholder={`Comment for the ${props.actionTaken}`}
          cols={50}
          rows={3}
          className={`border border-${
            props.error.element === "comment" ? "red" : "gray"
          }-400 rounded px-3 py-1 w-full focus:outline-none`}
          style={{ resize: "none" }}
          value={props.responseMsg}
          onChange={(e) => {
            props.setResponseMsg(e.target.value);
            props.setError({ element: "", msg: "" });
          }}
          disabled={props.loadingRequest}
        ></textarea>
        {props.error.element === "comment" && (
          <p className="text-red-800">{props.error.msg}</p>
        )}
      </div>
      <div className="flex flex-row gap-1 max-w-full px-2">
        <button
          className="bg-gray-800 px-4 py-2 font-semibold rounded text-white inline-flex items-center space-x-2 focus:outline-none"
          onClick={() => {
            props.setResponseMsg("");
            props.setActionTaken(null);
            props.setError({ element: "", msg: "" });
          }}
          disabled={props.loadingRequest}
        >
          <RiArrowGoBackFill className="w-5 h-5" />
          <span>Back</span>
        </button>
        <button
          className="bg-blue-700 px-4 py-2 font-semibold rounded text-white focus:outline-none"
          disabled={props.loadingRequest}
          onClick={() =>
            window.confirm(
              `Are you sure you want to ${props.actionTaken} this document?`
            ) && props.handleVerify()
          }
        >
          {props.loadingRequest ? (
            <div className="">Loading...</div>
          ) : (
            <div className="inline-flex items-center space-x-2 gap-1">
              <FaRegCheckCircle className="w-5 h-5" />
              <span>Confirm</span>
            </div>
          )}
        </button>
      </div>
      {/* end right part */}
    </div>
  );
};
interface FooterInitialPropInterface {
  doc: TableCandidatesInterface;
  setDocId: (data: string) => void;
  setAction: (value: null | "approval" | "rejection") => void;
  actionTaken: "approval" | "rejection" | null;
}

const FooterInitial: React.FC<FooterInitialPropInterface> = (
  props
): JSX.Element => {
  return (
    <div className="w-full flex md:flex-col flex-col justify-between items-start gap-2">
      {/* left part */}
      <div className="flex items-center gap-4">
        {/* doc title & status */}
        <section className="p-3 w-full">
          {/* --------------------------------- */}
          <div className="flex items-center gap-2 border-b-2 my-2 py-1">
            <ImFolderOpen className="text-blue-600 text-xl" />
            <h2 className="uppercase tracking-wide text-xl font-semibold text-gray-700">
              Document Decision Re-Override
            </h2>
          </div>
          <div className="flex flex-row items-start justify-left gap-2 mb-2">
            <span className="text-gray-500 italic mt-1">Doc Title: </span>
            <span className="font-bold text-2xl">
              {props.doc.main_doc_title}
            </span>
          </div>
          <div className="flex flex-row items-center justify-left gap-2 mb-2">
            <span className="text-gray-500 italic">Names: </span>
            <span className="font-medium text-lg">
              {props.doc.fname + " " + props.doc.lname}
            </span>
          </div>
          <section className="animate__animated animate__fadeIn">
            <div className="flex flex-row items-center justify-left gap-2 mb-2">
              <span className="text-gray-500 italic">National ID: </span>
              <span className="font-medium text-lg">{props.doc.n_id}</span>
            </div>
            <div className="flex flex-row items-center justify-left gap-2 mb-2">
              <span className="text-gray-500 italic">Phone: </span>
              <span className="font-medium text-lg">
                {props.doc.phone_number}
              </span>
            </div>
            <div className="flex flex-row items-center justify-left gap-2 mb-2">
              <span className="text-gray-500 italic">Email: </span>
              <span className="font-medium text-lg">{props.doc.email}</span>
            </div>
            {/* status */}
            <div className="flex items-center gap-1">
              <span className="text-gray-500 italic">Status:</span>
              <span
                className={`text-${
                  props.doc.validation_status === "waiting"
                    ? "yellow"
                    : props.doc.validation_status === "approved"
                    ? "blue"
                    : "red"
                }-700 font-medium text-lg`}
              >
                {props.doc.validation_status === "waiting"
                  ? "Pending"
                  : props.doc.validation_status === "approved"
                  ? "Approved"
                  : "Rejected"}
              </span>
            </div>
            {/* end status */}

            {/* comment */}
            {props.doc.validated_by && (
              <div className="w-full">
                <span className="text-gray-500 italic">Reason:</span>
                <p className={`text-gray-700 font-medium text-base`}>
                  {props.doc.validated_by?.comment}
                </p>
              </div>
            )}
            {/* end comment */}
          </section>
        </section>
        {/* end doc title & status */}
      </div>
      {/* end left part */}

      {/* right part */}
      {props.actionTaken === null && (
        <div className="flex flex-row md:flex-row justify-between px-3 w-full items-center gap-2 overflow-x-auto">
          <div className="flex items-center gap-2 px-2">
            {props.doc.validation_status === "approved" && (
              <button
                className={`px-4 py-2 font-semibold text-red-50 rounded inline-flex items-center space-x-2 focus:outline-none bg-red-800`}
                onClick={() => props.setAction("rejection")}
              >
                <MdCancel className="w-5 h-5" />
                <span>Reject</span>
              </button>
            )}
            {props.doc.validation_status === "rejected" && (
              <button
                className={`px-4 py-2 font-semibold text-blue-50 rounded inline-flex items-center space-x-2 focus:outline-none bg-blue-700`}
                onClick={() => props.setAction("approval")}
              >
                <FaRegCheckCircle className="w-5 h-5" />
                <span>Approve</span>
              </button>
            )}
          </div>

          <button
            className="bg-gray-700 px-4 py-2 rounded font-semibold text-white inline-flex items-center space-x-2 focus:outline-none"
            onClick={() => props.setDocId("")}
          >
            <MdClose className="w-5 h-5" />
            <span>Close</span>
          </button>
        </div>
      )}
      {/* end right part */}
    </div>
  );
};

interface AppProps {
  doc: TableCandidatesInterface;
  setDocId: (data: string) => void;
  responseMsg: string;
  loadingRequest: boolean;
  setResponseMsg: (state: string) => void;
  handleVerify: () => Promise<void>;
  setError: (data: { element: string; msg: string }) => void;
  actionTaken: null | "approval" | "rejection";
  setActionTaken: (state: null | "approval" | "rejection") => void;
  error: {
    element: string;
    msg: string;
  };
  isTeachingDocument: boolean;
  setIsTeachingDocument: (state: boolean) => void;
  hasDisabilityDocument: boolean;
  setHasDisabilityDocument: (state: boolean) => void;
  expiration_date: string;
  setExpirationDate: (value: string) => void;
}

interface AppStates {
  isFullscreen: boolean;
  failedToLoadFile: boolean;
  loadingImage: boolean;
  actionTaken: null | "approval" | "rejection";
}

class FileView extends React.Component<AppProps, AppStates> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      isFullscreen: false,
      failedToLoadFile: false,
      loadingImage: true,
      actionTaken: null,
    };
  }

  setSetFullscreen = (value: boolean) => {
    this.setState({ isFullscreen: value });
  };

  handleFullScreen = () => {
    if (this.state.isFullscreen) {
      // alert("closing");
      fullScreen("doc-img", "CLOSE");
      this.setSetFullscreen(false);
    } else {
      // alert("opening");
      this.setSetFullscreen(true);
      fullScreen("doc-img", "OPEN");
    }
  };

  render() {
    return (
      <div className="bg-gray-50 h-full md:h-screen grid grid-cols-1 md:grid-cols-3">
        {/* image content */}
        {this.props.doc.files.length > 0 && this.state.loadingImage && (
          <div className="col-span-2 max-h-full md:max-h-screen overflow-y-auto relative group">
            <div className="relative">
              {this.state.failedToLoadFile === false ? (
                <div className="w-full mx-auto mt-5 px-4">
                  <div className="animate-pulse flex space-x-4">
                    <div className="flex-1 space-y-4 py-4">
                      <div className="bg-gray-300 rounded h-72 flex items-center justify-center">
                        <h1 className="text-5xl text-gray-500 font-bold">
                          Loading{" "}
                          {isFileImage(getFileType(this.props.doc.files))
                            ? "image"
                            : "pdf"}
                          ...
                        </h1>
                      </div>
                    </div>
                  </div>
                </div>
              ) : (
                <div className="px-3">
                  <NoSession
                    title="Error"
                    description={
                      isFileImage(getFileType(this.props.doc.files))
                        ? "Failed to load image"
                        : "Failed to load pdf"
                    }
                  />
                </div>
              )}
            </div>
          </div>
        )}
        <div
          className={`col-span-2 h-auto md:max-h-screen overflow-y-auto relative bg-gray-200 group ${
            this.state.loadingImage && "hidden"
          }`}
        >
          {/* DISPLAY IMAGE OF PDF =================================== */}
          {this.props.doc.files.length > 0 ? (
            isFileImage(getFileType(this.props.doc.files)) ? (
              <div className="relative">
                <img
                  src={API.AUTH_FILES + this.props.doc.files}
                  alt=""
                  className={`w-full md:w-auto h-auto md:max-h-screen mx-auto ${
                    this.state.loadingImage && "hidden"
                  }`}
                  id="doc-img"
                  onLoad={() => this.setState({ loadingImage: false })}
                  onError={() => this.setState({ failedToLoadFile: true })}
                  onDoubleClick={() => this.handleFullScreen()}
                />
              </div>
            ) : (
              <ViewPDF
                file_url={API.AUTH_FILES + this.props.doc.files}
                class_name={`w-full h-screen md:max-h-screen mx-auto ${
                  this.state.loadingImage && "hidden"
                }`}
                frame_title={"application PDF file view"}
                setLoadingFile={(e: boolean) =>
                  this.setState({ loadingImage: e })
                }
                setLoadingError={(e: boolean) =>
                  this.setState({ failedToLoadFile: e })
                }
              />
            )
          ) : (
            <div className="text-center text-red-400 py-8 animate__animated animate__fadeIn">
              <CgImage className="mx-auto w-40 h-40" />
              <span className="font-semibold text-4xl">No Files Provided</span>
            </div>
          )}
          {/* END DISPLAY IMAGE OF PDF =================================== */}

          {/* ------------------------------------------------------------ */}

          {/* FILE VIEW CONTROLLERS =================================== */}
          <div
            className={`flex items-center justify-between absolute top-0 ${
              isFileImage(getFileType(this.props.doc.files))
                ? "w-full"
                : "w-max"
            } px-2 py-2`}
          >
            {this.props.doc.files.length > 0 &&
              isFileImage(getFileType(this.props.doc.files)) && (
                <div className="flex items-center gap-2 opacity-0 group-hover:opacity-100">
                  <button
                    className="flex items-center bg-gray-100 justify-center h-10 w-10 rounded-full shadow-md opacity-4 cursor-pointer hover:bg-blue-50"
                    onClick={() => zoomInOut("doc-img", "IN")}
                  >
                    <BiZoomIn className="h-6 w-6 text-gray-500" />
                  </button>
                  <button
                    className="flex items-center bg-gray-100 justify-center h-10 w-10 rounded-full shadow-md opacity-4 cursor-pointer hover:bg-blue-50"
                    onClick={() => zoomInOut("doc-img", "OUT")}
                  >
                    <BiZoomOut className="h-6 w-6 text-gray-500" />
                  </button>
                  <div
                    className="flex items-center bg-gray-100 justify-center h-10 w-10 rounded-full shadow-md opacity-4 cursor-pointer hover:bg-blue-50"
                    onClick={() => fullScreen("doc-img", "OPEN")}
                  >
                    <AiOutlineFullscreen className="h-6 w-6 text-gray-500" />
                  </div>
                </div>
              )}
          </div>
          {/* END FILE VIEW CONTROLLERS =================================== */}
        </div>

        {/* footer */}
        <div>
          {this.props.doc.files && (
            <FooterInitial
              doc={this.props.doc}
              setDocId={this.props.setDocId}
              setAction={this.props.setActionTaken}
              actionTaken={this.props.actionTaken}
            />
          )}
          {this.props.actionTaken !== null && (
            <FooterResponse
              actionTaken={this.props.actionTaken}
              responseMsg={this.props.responseMsg}
              loadingRequest={this.props.loadingRequest}
              error={this.props.error}
              isTeachingDocument={this.props.isTeachingDocument}
              setIsTeachingDocument={this.props.setIsTeachingDocument}
              hasDisabilityDocument={this.props.hasDisabilityDocument}
              setHasDisabilityDocument={this.props.setHasDisabilityDocument}
              setActionTaken={this.props.setActionTaken}
              setResponseMsg={this.props.setResponseMsg}
              handleVerify={this.props.handleVerify}
              setError={this.props.setError}
              expiration_date={this.props.expiration_date}
              setExpirationDate={this.props.setExpirationDate}
              disability={false}
              in_education={false}
              expiration={false}
            />
          )}
        </div>
        {/* end footer */}
      </div>
    );
  }
}

export default FileView;
