import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { API } from "../utils/api";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";
import {
  fetchUserAccessInterface,
  UserAccessInterface,
  userPageLimit,
  VerifyDocumentInterface,
} from "./users";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface GetUserDocumentsAction {
  type: ActionTypes.GET_CANDIDATE_DOCUMENTS;
  payload: UserAccessInterface;
}

export interface AllVerifyDocumentAction {
  type: ActionTypes.ALL_VERIFY_DOCUMENT;
  payload: { data: VerifyDocumentInterface; page: number };
}

/**
 * * ****************************** ACTIONS *****************************
 */

export const getCandidateDocuments = (page: number, callback?: Function) => {
  callback && callback(true, "");
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<fetchUserAccessInterface>(
        `${API.USER}/docs/remainingdocs?page=${page}&limit=${userPageLimit}`
      );
      console.log({ getCandidateDocuments: res });
      dispatch<GetUserDocumentsAction>({
        type: ActionTypes.GET_CANDIDATE_DOCUMENTS,
        payload: {
          users: [{ data: res.data.users!, page: page }],
          next: res.data.next,
          previous: res.data.previous,
          total_users: res.data.total_users,
        },
      });
      callback && callback(false, "", res.data.users);
    } catch (error) {
      console.error({ user_error: error });
      callback && callback(false, errorToText(error));
    }
  };
};

export const allVerifyDocument = (
  data: VerifyDocumentInterface,
  page: number
) => {
  return (dispatch: Dispatch) => {
    dispatch<AllVerifyDocumentAction>({
      type: ActionTypes.ALL_VERIFY_DOCUMENT,
      payload: { data, page },
    });
  };
};
