import React, { Component } from "react";
import { MdFolderSpecial } from "react-icons/md";
import { RiFolderUserFill, RiFolderWarningFill } from "react-icons/ri";
import { connect } from "react-redux";
import { NavLink } from "react-router-dom";
import { Auth } from "../../actions";
import { isDocumentValidationOpen } from "../../interfaces/document_validation";
import { StoreState } from "../../reducers";
import { FC_HasInstitute } from "../../utils/functions";
import AllCandidates from "./AllCandidates/AllCandidates";
import DocumentApplChecking from "./DocumentApplChecking/DocumentApplChecking";
import DocumentReEvaluation from "./DocumentReEvaluation/DocumentReEvaluation";

enum DocumentValidationTypes {
  CANDIDATES = "Document validation all candidates",
  APPLICATION = "Document validation applied candidates",
  RE_EVALUATION = "Document Re-Validation",
}

type AppProps = {
  auth: Auth;
};

type AppState = {
  loading: boolean;
  pageToLoad: DocumentValidationTypes | null;
};

class _DocumentValidation extends Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
      pageToLoad: null,
    };
  }

  setLoadingState = (value: boolean) => {};
  componentDidMount() {
    if (this.state.loading && this.state.pageToLoad === null) {
      if (
        isDocumentValidationOpen(this.props.auth.application_session) &&
        FC_HasInstitute(this.props.auth.education_institution_id)
      ) {
        this.setState({
          loading: false,
        });
      } else {
        this.setState({
          loading: false,
          pageToLoad: DocumentValidationTypes.CANDIDATES,
        });
        // setPageToLoad(DocumentValidationTypes.CANDIDATES);
      }
    }
  }

  render() {
    if (this.state.loading) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading...
        </div>
      );
    }
    if (this.state.pageToLoad === null) {
      return (
        <div className="min-w-screen h-screen fixed flex justify-center items-center inset-0 z-50">
          <div className="absolute bg-black opacity-80 inset-0 z-0"></div>
          <section className="max-w-3xl p-5 relative mx-auto my-auto rounded-md bg-gray-100 w-full">
            <div className="w-full flex flex-row items-center justify-center">
              <div
                className="bg-white rounded-3xl p-2 shadow-sm m-3 cursor-pointer text-center hover:bg-blue-50 border-2 hover:border-blue-500"
                onClick={() =>
                  this.setState({
                    pageToLoad: DocumentValidationTypes.CANDIDATES,
                  })
                }
              >
                <RiFolderUserFill className="h-40 text-blue-600 rounded-2xl w-full object-cover" />
                <p className="mx-4 text-xl font-semibold text-gray-900 -mt-3">
                  Validate all candidates
                </p>
              </div>

              <div
                className="bg-white rounded-3xl p-2 shadow-sm m-3 cursor-pointer text-center hover:bg-blue-50 border-2 hover:border-blue-500"
                onClick={() =>
                  this.setState({
                    pageToLoad: DocumentValidationTypes.APPLICATION,
                  })
                }
              >
                <MdFolderSpecial className="h-40 text-blue-600 rounded-2xl w-full object-cover" />
                <p className="mx-4 text-xl font-semibold text-gray-900 -mt-3">
                  Validate applied candidates
                </p>
              </div>

              <div
                className="bg-white rounded-3xl p-2 shadow-sm m-3 cursor-pointer text-center hover:bg-blue-50 border-2 hover:border-blue-500"
                onClick={() =>
                  this.setState({
                    pageToLoad: DocumentValidationTypes.RE_EVALUATION,
                  })
                }
              >
                <RiFolderWarningFill className="h-32 my-4 text-blue-600 rounded-2xl w-full object-cover" />
                <p className="mx-4 text-xl font-semibold text-gray-900 -mt-3">
                  {DocumentValidationTypes.RE_EVALUATION}
                </p>
              </div>
            </div>

            <div className="text-center group text-sm -mb-3 mt-1">
              <NavLink
                to="/apps-page"
                className="border border-rounded-md rounded bg-gray-100 border-gray-100 group-hover:bg-gray-50 group-hover:border-gray-400 px-2 py-1"
              >
                Back to App
              </NavLink>
            </div>
          </section>
        </div>
      );
    }
    if (this.state.pageToLoad === DocumentValidationTypes.APPLICATION) {
      return <DocumentApplChecking />;
    }

    if (this.state.pageToLoad === DocumentValidationTypes.RE_EVALUATION) {
      return <DocumentReEvaluation />;
    }

    return <AllCandidates />;
  }
}

const mapStateToProps = ({
  auth,
}: StoreState): {
  auth: Auth;
} => {
  return {
    auth: auth,
  };
};

export const DocumentValidation = connect(mapStateToProps)(_DocumentValidation);
