import React, { useEffect, useState } from "react";

type CountButtonProps = {
  title: string;
  className: string;
  time: number;
  onClick: Function;
};

const CountButton: React.FC<CountButtonProps> = (props) => {
  const [seconds, setSeconds] = useState<number>(props.time);
  useEffect(() => {
    let myInterval = setInterval(() => {
      if (seconds > 0) {
        setSeconds(seconds - 1);
      }
      if (seconds === 0) {
        clearInterval(myInterval);
      }
    }, 1000);
    return () => {
      clearInterval(myInterval);
    };
  });

  return (
    <>
      {seconds === 0 ? null : (
        <button
          className={`flex items-center gap-2 bg-green-600 hover:bg-green-700 focus:outline-none rounded p-2 text-white font-semibold ${props.className}`}
          onClick={() => props.onClick()}
        >
          <div className="rounded-full bg-white text-green-600 p-1 w-8 h-8">
            <h1>{seconds < 10 ? `0${seconds}` : seconds}</h1>
          </div>
          {props.title}
        </button>
      )}
    </>
  );
};

export default React.memo(CountButton);
