import axios from "axios";
import React, { Component, Fragment } from "react";
import { ImFilesEmpty } from "react-icons/im";
import { IoDocumentOutline } from "react-icons/io5";
import { connect } from "react-redux";
import {
  Auth,
  positionsStoreInterface,
  PushPopupAlert,
} from "../../../actions";
import {
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
} from "../../../actions/document";
import {
  FC_GetUserBySearch,
  VerifyDocumentInterface,
} from "../../../actions/users";
import { PopupAlertData } from "../../../components/Alert/Popup";
import TableLoading from "../../../components/Loading/TableLoading";
import { GetDocTitle } from "../../../interfaces/document_validation";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import { DocumentReducerInterface } from "../../../reducers/document";
import {
  capitalizeFirstLetter,
  isEmptyOrSpaces,
} from "../../../shared/dataTest";
import {
  DocValidationStatusInterface,
  UserDocumentsInterface,
  UserInterface,
} from "../../../shared/interfaces";
import { API } from "../../../utils/api";
import { errorToText } from "../../../utils/errors";
import { FC_GetBase64Img } from "../../../utils/functions";
import { TableCandidatesInterface } from "../DocumentApplChecking/ContentByDocument";
import FileView from "./FileView";
import profile from "../../../shared/blank_profile.png"; // with im
import {
  ApplicationStatesDescription,
  GetApplicationsAPI,
} from "../../../interfaces/application";
import Loading from "../../../components/Loading/Loading";
import PositionDetails from "../../CandidateApplication/ApplicationComponents/PositionDetails";
import { BsBriefcase } from "react-icons/bs";
import { loadAllPositions } from "../../../actions";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { PositionAPI } from "../../../interfaces";

type AppProps = {
  auth: Auth;
  documentReducer: DocumentReducerInterface;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
  positions: positionsStoreInterface;
  loadAllPositions: (callback: (loading: boolean) => void) => void;
};
type AppStates = {
  loading: boolean;
  loadingUser: boolean;
  loadingApplications: boolean;
  manageUser: UserInterface | null;
  loadingRequest: boolean;
  searchFind: string;
  searchError: string;
  doc_id: string;
  responseMsg: string;
  isTeachingDocument: boolean;
  hasDisabilityDocument: boolean;
  error: { element: string; msg: string };
  actionTaken: null | "approval" | "rejection";
  expiration_date: string;
  candidateApplications: GetApplicationsAPI[] | null;
  viewPositionDetails: PositionAPI | null;
};
class DocumentReEvaluation extends Component<AppProps, AppStates> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
      loadingUser: false,
      manageUser: null,
      loadingRequest: false,
      searchFind: "",
      searchError: "",
      doc_id: "",
      responseMsg: "",
      isTeachingDocument: false,
      hasDisabilityDocument: false,
      error: { element: "", msg: "" },
      actionTaken: null,
      expiration_date: "",
      candidateApplications: null,
      loadingApplications: false,
      viewPositionDetails: null,
    };
  }

  GetCandidateApplications = async (user_id: string) => {
    this.setState({ loadingApplications: true });
    try {
      const res = await axios.get<GetApplicationsAPI[]>(
        `${API.APPLICATION}/apply/user/${user_id}`
      );
      if (res) {
        this.setState({
          candidateApplications: res.data,
        });
        if (this.props.positions.positions_list === null) {
          this.props.loadAllPositions((status: boolean) => {
            this.setState({ loadingApplications: status });
          });
        } else {
          this.setState({ loadingApplications: false });
        }
      }
    } catch (error: any) {
      alert("Failed to load candidate Applications");
      this.setState({ candidateApplications: [], loadingApplications: false });
      console.log("Error: ", error);
    }
  };

  componentDidMount = () => {
    if (
      this.props.documentReducer.documentCategory === null ||
      this.props.documentReducer.documentCategory.length <= 0
    ) {
      // LOADING DOCUMENT CATEGORIES ===============================
      this.props.getDocumentCategory((status: boolean) => {
        this.setState({ loading: status });
      });
    } else {
      this.setState({ loading: false });
    }

    // LOADING DOCUMENT SUB CATEGORIES =========================
    if (
      this.props.documentReducer.documentSubCategory === null ||
      this.props.documentReducer.documentSubCategory.length <= 0
    ) {
      this.props.getDocumentSubCategory((status: boolean) => {
        this.setState({ loading: status });
      });
    } else {
      this.setState({ loading: false });
    }

    // LOADING DOCUMENT FIELDS =========================
    if (
      this.props.documentReducer.documentFields === null ||
      this.props.documentReducer.documentFields.length <= 0
    ) {
      this.props.getDocumentFields((status: boolean) => {
        this.setState({ loading: status });
      });
    } else {
      this.setState({ loading: false });
    }
  };

  FC_DoSearch = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (isEmptyOrSpaces(this.state.searchFind)) {
      return this.setState({ searchError: "Required!" });
    }

    if (
      this.state.candidateApplications !== null ||
      this.state.viewPositionDetails !== null
    ) {
      this.setState({ candidateApplications: null, viewPositionDetails: null });
    }

    this.setState({ manageUser: null });
    FC_GetUserBySearch(
      this.state.searchFind,
      (status: boolean, msg: string, data: UserInterface | null | "") => {
        this.setState({ loadingUser: status });
        if (status === false) {
          if (!isEmptyOrSpaces(msg)) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          } else if (data === null || data === "") {
            this.props.PushPopupAlert({
              title: "User not found!",
              theme: PopupAlertTheme.warning,
            });
          } else {
            console.log("===================--------=================");
            console.log(data);
            console.log("===================--------=================");
            this.setState({ manageUser: data });
            this.GetCandidateApplications(data.user_id);
          }
        }
      }
    );
  };

  FC_DocumentInArray = (
    docCategories: DocumentReducerInterface,
    DocumentsData: UserInterface
  ): TableCandidatesInterface[] | [] => {
    let documentsTemp: TableCandidatesInterface[] = [];
    let details = DocumentsData.details;
    for (let d in details) {
      let candidate_detail: UserDocumentsInterface = DocumentsData.details[d];
      if (
        candidate_detail.files !== null &&
        candidate_detail.files.length > 0 &&
        candidate_detail.validation_status !== "waiting"
      ) {
        documentsTemp.push({
          // DATA FROM MAIN PROPERTY
          user_id: DocumentsData.user_id,
          n_id: DocumentsData.n_id ? DocumentsData.n_id : "",
          fname: DocumentsData.user_info.fname,
          lname: DocumentsData.user_info.lname,
          phone_number: DocumentsData.phone_number,
          district: DocumentsData.user_info.district,
          email: DocumentsData.email,
          // NEW DATA GENERATED BAISED ON DETAILS PROPERTY
          main_doc_title: GetDocTitle(docCategories, candidate_detail).main,
          // DATA FROM DETAILS PROPERTY
          category_id: candidate_detail.category_id,
          sub_category_id: candidate_detail.sub_category_id,
          category_subcategory_data: candidate_detail.category_subcategory_data,
          title: candidate_detail.title,
          description: candidate_detail.description,
          files: candidate_detail.files,
          start_year: candidate_detail.start_year,
          end_year: candidate_detail.end_year,
          school: candidate_detail.school.title,
          details_email: candidate_detail.email,
          names: candidate_detail.names,
          details_phone_number: candidate_detail.phone_number,
          company: candidate_detail.company,
          year: candidate_detail.year,
          doc_id: candidate_detail.doc_id,
          validation_status: candidate_detail.validation_status,
          validated_by: candidate_detail.validated_by
            ? {
                user_id: candidate_detail.validated_by.user_id,
                comment: candidate_detail.validated_by.comment
                  ? candidate_detail.validated_by.comment
                  : candidate_detail.validated_by.commet,
                validation_status:
                  candidate_detail.validated_by.validation_status,
                date: candidate_detail.validated_by.date,
              }
            : null,
          doc_history_status: "",
          get_in_date: "",
          approved: candidate_detail.approved,
          // DATA FROM EXAMS PROPERTY
          exams: [],
        });
      }
    }
    return documentsTemp;
  };

  FC_shortListing = async () => {
    if (this.state.manageUser) {
      try {
        const res = await axios.patch(
          `${API.APPLICATION}/apply/doccheckshortlist/${this.state.manageUser.user_id}`,
          {
            email: this.state.manageUser.email,
            phone_number: this.state.manageUser.phone_number,
          }
        );
        console.log("================FC_shortListing====================");
        console.log({ res });
        console.log("================FC_shortListing====================");
      } catch (error) {
        console.error({ error });
        this.props.PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
    }
  };

  FC_ValidateCandidateInState = (api_body: VerifyDocumentInterface) => {
    let candidate_copy = this.state.manageUser;

    if (candidate_copy) {
      let doc_index = candidate_copy.details.findIndex(
        (item) => item.doc_id === api_body.doc_id
      );
      if (doc_index >= 0) {
        candidate_copy.details[doc_index].validation_status =
          api_body.validation_status;
        candidate_copy.details[doc_index].validated_by = {
          user_id: this.props.auth.user.user_id,
          commet: api_body.validated_by.comment,
          comment: api_body.validated_by.comment,
          validation_status: api_body.validation_status,
          date: Date(),
        };
      }
    }
    this.setState({ manageUser: candidate_copy });
  };

  handleVerify = async () => {
    if (this.state.manageUser && !isEmptyOrSpaces(this.state.doc_id)) {
      if (isEmptyOrSpaces(this.state.responseMsg)) {
        return this.setState({
          error: {
            element: "comment",
            msg: `Reason of ${this.state.actionTaken} is required`,
          },
          loadingRequest: false,
        });
      }

      this.setState({
        loadingRequest: true,
      });

      const api_body: VerifyDocumentInterface = {
        user_id: this.state.manageUser.user_id,
        doc_id: this.state.doc_id,
        validation_status:
          this.state.actionTaken === "approval"
            ? DocValidationStatusInterface.APPROVED
            : DocValidationStatusInterface.REJECTED,
        validated_by: {
          user_id: this.props.auth.user.user_id,
          comment: this.state.responseMsg,
          validation_status:
            this.state.actionTaken === "approval"
              ? DocValidationStatusInterface.APPROVED
              : DocValidationStatusInterface.REJECTED,
        },
        in_education: this.state.isTeachingDocument
          ? this.state.isTeachingDocument
          : this.state.manageUser.in_education,
        disability: this.state.hasDisabilityDocument
          ? this.state.hasDisabilityDocument
          : this.state.manageUser.disability,
        expiration_date: this.state.expiration_date,
      };

      try {
        const res = await axios.post<VerifyDocumentInterface>(
          `${API.USER}/docs/validateuserdoc`,
          api_body
        );
        if (res) {
          await this.FC_shortListing();
          this.FC_ValidateCandidateInState(api_body);
          //   this.setState({ undo_decision: true });
          //   setTimeout(() => {
          //     this.setState({ undo_decision: false });
          //   }, 20000); // 20 seconds
          this.props.PushPopupAlert({
            title: `Document ${
              this.state.actionTaken === "approval" ? "approved" : "rejected"
            } successful`,
            theme: PopupAlertTheme.success,
          });

          this.setState({
            responseMsg: "",
            actionTaken: null,
            hasDisabilityDocument: false,
            isTeachingDocument: false,
            error: { element: "", msg: "" },
          });
        }
      } catch (error) {
        console.error(error);
        this.props.PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
      this.setState({ loadingRequest: false });
    }
  };

  render() {
    if (this.state.loading === true) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading...
        </div>
      );
    }

    const DATA_Documents = this.state.manageUser
      ? this.FC_DocumentInArray(
          this.props.documentReducer,
          this.state.manageUser
        )
      : null;

    const Data_ThisDocument = DATA_Documents
      ? DATA_Documents.find((item) => item.doc_id === this.state.doc_id)
      : undefined;

    const Data_Waiting = this.state.manageUser
      ? this.state.manageUser.details.filter(
          (item) => item.validation_status === "waiting"
        )
      : [];

    if (Data_ThisDocument) {
      return (
        <FileView
          doc={Data_ThisDocument}
          setDocId={(value: string) => this.setState({ doc_id: value })}
          responseMsg={this.state.responseMsg}
          loadingRequest={this.state.loadingRequest}
          setResponseMsg={(value: string) =>
            this.setState({ responseMsg: value })
          }
          handleVerify={this.handleVerify}
          setError={(value: { element: string; msg: string }) =>
            this.setState({ error: value })
          }
          actionTaken={this.state.actionTaken}
          setActionTaken={(value: "approval" | "rejection" | null) =>
            this.setState({ actionTaken: value })
          }
          error={this.state.error}
          isTeachingDocument={this.state.isTeachingDocument}
          setIsTeachingDocument={(value: boolean) =>
            this.setState({ isTeachingDocument: value })
          }
          hasDisabilityDocument={this.state.hasDisabilityDocument}
          setHasDisabilityDocument={(value: boolean) =>
            this.setState({ hasDisabilityDocument: value })
          }
          expiration_date={this.state.expiration_date}
          setExpirationDate={(value: string) =>
            this.setState({ expiration_date: value })
          }
        />
      );
    }

    return (
      <Fragment>
        <div className="w-full my-5">
          <section className="">
            <form
              onSubmit={this.FC_DoSearch}
              className={`w-full mt-8 bg-transparent border-2 mx-auto rounded-md lg:max-w-sm ${
                !isEmptyOrSpaces(this.state.searchError)
                  ? "border-red-500 dark:border-red-700 focus-within:border-red-400 dark:focus-within:border-red-400"
                  : "border-gray-500 dark:border-gray-700 focus-within:border-gray-400 dark:focus-within:border-gray-400"
              }`}
            >
              <div className="flex flex-col lg:flex-row">
                <input
                  type="text"
                  placeholder="Search by NID or Phone or Email"
                  className="flex-1 h-10 px-4 py-2 m-1 text-gray-700 placeholder-gray-400 bg-transparent border-none appearance-none dark:text-gray-200 focus:outline-none focus:placeholder-transparent focus:ring-0"
                  disabled={
                    this.state.loadingUser ||
                    this.state.loadingRequest ||
                    this.state.loading
                  }
                  onChange={(e) =>
                    this.setState({
                      searchFind: e.target.value,
                      searchError: "",
                    })
                  }
                  value={this.state.searchFind}
                />

                <button
                  type="submit"
                  className="h-10 px-4 py-2 m-1 text-sm text-white transition-colors duration-200 transform bg-gray-500 rounded-md hover:bg-gray-400 focus:outline-none focus:bg-gray-400"
                  disabled={
                    this.state.loadingUser ||
                    this.state.loadingRequest ||
                    this.state.loading
                  }
                >
                  {this.state.loadingUser ? "Loading" : "Search"}
                </button>
              </div>
              {!isEmptyOrSpaces(this.state.searchError) && (
                <span className="text-red-500">{this.state.searchError}</span>
              )}
            </form>
          </section>

          {/* DISPLAY PROFILE ==================================================== */}
          {this.state.manageUser && (
            <section className="flex items-start mb-4 mt-3 py-3 px-4 rounded bg-white">
              <div className="w-auto">
                <img
                  src={
                    this.state.manageUser.user_info.photo !== "pic" &&
                    this.state.manageUser.user_info.photo !== undefined
                      ? FC_GetBase64Img(this.state.manageUser.user_info.photo)
                      : profile
                  }
                  className="w-auto object-center object-cover h-32 rounded-lg"
                  alt=""
                />
              </div>
              <section className="w-full -m-1">
                <div className="flex items-center gap-2 px-3">
                  <span className={`italic text-gray-600`}>Names:</span>
                  <span className="text-gray-800 font-bold text-xl">
                    {this.state.manageUser.user_info.fname +
                      " " +
                      this.state.manageUser.user_info.lname}
                  </span>
                </div>
                <div className="flex items-center gap-2 px-3">
                  <span className={`italic text-gray-600`}>NID:</span>
                  <span className="text-gray-800 font-bold text-xl">
                    {this.state.manageUser.n_id}
                  </span>
                </div>
                <div className="flex items-center gap-2 px-3">
                  <span className={`italic text-gray-600`}>Sex:</span>
                  <span className="text-gray-800 font-bold text-xl">
                    {this.state.manageUser.user_info.sex}
                  </span>
                </div>
                <div className="flex items-center gap-2 px-3">
                  <span className={`italic text-gray-600`}>Phone:</span>
                  <span className="text-gray-800 font-bold text-xl">
                    {this.state.manageUser.phone_number}
                  </span>
                </div>
                <div className="flex items-center gap-2 px-3">
                  <span className={`italic text-gray-600`}>Email:</span>
                  <span className="text-gray-800 font-bold text-xl">
                    {this.state.manageUser.email}
                  </span>
                </div>
                <span className="rounded bg-green-600 text-white text-lg py-1 px-2 mx-3">
                  Waiting documents: {Data_Waiting.length}
                </span>
              </section>
            </section>
          )}

          {/* Candidate applications */}
          <section className="mt-3 bg-white rounded border">
            {this.state.viewPositionDetails === null ? (
              this.state.loadingApplications === false &&
              this.state.candidateApplications !== null && (
                <div className="border-b border-gray-400 p-3">
                  <div className="font-light text-4xl">User applications</div>
                  <div>List of application for the current session</div>
                </div>
              )
            ) : (
              <div className="border-b border-gray-400 p-3 flex flex-row items-center gap-3 w-full">
                <div
                  onClick={() => this.setState({ viewPositionDetails: null })}
                  className="btn bg-blue-100 rounded cursor-pointer hover:bg-blue-200 hover:text-blue-700"
                >
                  Back to list
                </div>
                <div className="">
                  <div className="font-light text-2xl text-blue-600">
                    {this.state.viewPositionDetails.position_title}
                  </div>
                  <div className="font-bold text-yellow-900 underline">
                    {
                      this.state.candidateApplications?.find(
                        (itm) =>
                          itm.position.position_id ===
                          this.state.viewPositionDetails?.position_id
                      )?.application_status
                    }
                  </div>
                </div>
              </div>
            )}
            {this.state.viewPositionDetails === null &&
            this.state.manageUser !== null ? (
              <div className="p-3">
                {this.state.loadingApplications === true ? (
                  <div>
                    <div className="mt-2 mx-3">Loading applications</div>
                    <Loading />
                  </div>
                ) : this.state.candidateApplications === null ||
                  this.state.candidateApplications.length === 0 ? (
                  <div className="flex items-center justify-center p-4 rounded bg-gray-100 text-center text-2xl font-light">
                    No applications found for this user
                  </div>
                ) : (
                  this.state.candidateApplications.map((item, i) => (
                    <div
                      key={i + 1}
                      className="bg-white border border-gray-300 rounded-md p-2 px-3 flex flex-row items-center gap-3 mb-3 justify-between group"
                    >
                      <div className="flex flex-row items-center gap-3 ">
                        <div>
                          <BsBriefcase className="text-4xl text-gray-500" />
                        </div>
                        <div className="flex flex-col text-sm">
                          <div className="flex flex-row items-center gap-2">
                            <span className="text-xl font-bold text-black">
                              {item.position.position_title}
                            </span>
                          </div>
                          <div className="flex flex-row items-center gap-2">
                            <span className="text-gray-600">Session:</span>
                            <span className="text-gray-600">
                              {item.application_session.session_plan_name}
                            </span>
                          </div>
                          <div className="flex flex-row items-center gap-2">
                            <span className="text-gray-600">Location:</span>
                            <span className="text-gray-600">
                              Province: {item.position.province_name}
                            </span>
                          </div>
                          <div className="flex flex-row items-center gap-2">
                            <span className="text-gray-600">Status:</span>
                            <div className="bg-yellow-100 px-3 border border-yellow-300 rounded-full">
                              <span className="text-yellow-900 font-extrabold">
                                {item.application_status}
                                {/* {
                              ApplicationStatesDescription.find(
                                (itm) => itm.key === item.application_status
                              )?.caption
                            } */}
                              </span>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div>
                        <div
                          onClick={() => {
                            let test =
                              this.props.positions.positions_list === null
                                ? undefined
                                : this.props.positions.positions_list.find(
                                    (itm) =>
                                      itm.position_id ===
                                      item.position.position_id
                                  );
                            if (test !== undefined) {
                              this.setState({
                                viewPositionDetails:
                                  this.props.positions.positions_list === null
                                    ? null
                                    : test,
                              });
                            }
                          }}
                          className="btn bg-blue-100 hover:bg-blue-200 rounded text-blue-700 cursor-pointer"
                        >
                          Details
                        </div>
                      </div>
                    </div>
                  ))
                )}
              </div>
            ) : (
              this.state.viewPositionDetails !== null && (
                <PositionDetails
                  positionDetails={this.state.viewPositionDetails}
                  cancel={() => this.setState({ viewPositionDetails: null })}
                  apply={() => this.setState({ viewPositionDetails: null })}
                  auth={this.props.auth}
                  showRegister={function (status: boolean): void {
                    throw new Error("Function not implemented.");
                  }}
                />
              )
            )}
          </section>

          {/* DISPLAY POSITION ==================================================== */}

          <section className="bg-white rounded w-full mt-3">
            <table className="w-full rounded shadow">
              <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
                <tr>
                  <td className="px-2 py-2">#</td>
                  {/* <td className="px-2 py-2">Names</td>
                <td className="px-2 py-2">National ID</td> */}
                  <td className="px-2 py-2">User Document</td>
                  <td className="px-2 py-2">Status</td>
                  <td className="px-2 py-2"></td>
                </tr>
              </thead>
              <tbody>
                {this.state.loadingUser && <TableLoading cols={4} />}
                {this.state.loadingUser === false && DATA_Documents === null && (
                  <tr className="border-b border-gray-200">
                    <td colSpan={4}>
                      <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                        <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                        <span className="text-base font-bold">
                          No User Details!
                        </span>
                        <div className="w-full mt-2 flex flex-row items-center justify-center">
                          <ul className="text-base py-2 text-blue-600 font-bold flex flex-row gap-2">
                            <li className="text-gray-600">
                              Find user by National ID or Phone number or Email
                            </li>
                          </ul>
                        </div>
                      </div>
                    </td>
                  </tr>
                )}
                {DATA_Documents && DATA_Documents.length === 0 && (
                  <tr className="border-b border-gray-200">
                    <td colSpan={4}>
                      <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                        <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                        <span className="text-base font-bold">
                          User Documents!
                        </span>
                        <div className="w-full mt-2 flex flex-row items-center justify-center">
                          <ul className="text-base py-2 text-blue-600 font-bold flex flex-row gap-2">
                            <li className="text-gray-600">
                              No approved or rejected documents found for this
                              user!
                            </li>
                          </ul>
                        </div>
                      </div>
                    </td>
                  </tr>
                )}
                {DATA_Documents &&
                  DATA_Documents.length > 0 &&
                  DATA_Documents.map((doc, index) => (
                    <tr
                      key={doc.doc_id + index + Math.random()}
                      className="border-b border-gray-200 cursor-pointer group hover:bg-gray-100"
                      onClick={() => this.setState({ doc_id: doc.doc_id })}
                    >
                      <td className="p-3 text-left whitespace-nowrap">
                        {index + 1}
                      </td>
                      <td className="py-3 px-3 text-left font-medium text-lg">
                        {doc.main_doc_title}
                      </td>
                      <td className="py-3 px-5 text-left">
                        <span
                          className={`text-sm font-medium py-1 px-2 rounded align-middle ${
                            doc.validation_status === "approved"
                              ? "bg-green-800 text-white"
                              : "bg-red-800 text-white"
                          }`}
                        >
                          {capitalizeFirstLetter(doc.validation_status)}
                        </span>
                      </td>
                      <td className="text-right">
                        <button
                          className="bg-blue-500 group-hover:bg-blue-600 text-blue-50 font-bold py-1 px-1 inline-flex rounded focus:outline-none"
                          title="View documents"
                          // disabled={props.loadingRequest}
                          // onClick={() => {
                          //   props.setViewDoc(true);
                          //   props.setDocumentId(doc.doc_id);
                          //   props.setCandidateId(doc.user_id);
                          // setModalState(true);document
                          // }}
                        >
                          <IoDocumentOutline className="w-5 h-5" />
                          View doc
                        </button>
                      </td>
                    </tr>
                  ))}
              </tbody>
            </table>
          </section>
        </div>
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  document,
  positions,
}: StoreState): {
  auth: Auth;
  documentReducer: DocumentReducerInterface;
  positions: positionsStoreInterface;
} => {
  return {
    auth: auth,
    documentReducer: document,
    positions: positions,
  };
};

export default React.memo(
  connect(mapStateToProps, {
    getDocumentCategory,
    getDocumentSubCategory,
    getDocumentFields,
    PushPopupAlert,
    loadAllPositions,
  })(DocumentReEvaluation)
);
